*, *::before, *::before {
  box-sizing: border-box; }

// Sets the base font-size to the base to use nicely on REM units
html {
  font-size: ( $base__font-size / 16px * 100% );
  min-height: 100vh;
  background: {
    color: $black;
    image: linear-gradient(rgba(0,0,0,0.47), rgba(0,0,0,0.47)), url('../images/gradient-background.jpg');
    size: cover;
    position: center;
    repeat: no-repeat; } }

body {
  margin-top: 75px; }

::selection {
  background: lighten($main-color, 12%);
  color: $white; }


img {
  max-width: 100%; }

.is-hidden {
  position: absolute;
  height: 0;
  width: 0;
  visibility: hidden; }

// Style for the SVGs
.icon {
  display: block;
  width: 100%;
  fill: currentColor;
  stroke: currentColor;
  margin: 0 auto; }

// Clearfix
.clearfix {
  @include cf; }
