%o-pane {
  background-color: $black;
  margin-bottom: 50px;
  padding-top: 20px;
  padding-left: 28px;
  padding-right: 28px;
  padding-bottom: 45px;
  box-shadow: 0 5px 5px rgba(0,0,0,0.2);

  header {
    display: flex;
    align-items: center;

    .o-pane__dot {
      @include size(12px);
      content: '';
      margin-right: 20px;
      background-color: $red;
      border-radius: 50%; }

    h2 {
      font-size: 15px; } }

  nav {
    width: calc(100% + 56px);
    margin-left: -28px;
    padding: 0 58px;
    box-shadow: 0 5px 5px rgba(0,0,0,0.1);
    margin-bottom: 20px;
    display: flex;
    align-items: center;

    a {
      text-transform: uppercase;
      font-size: 12px;
      display: block;
      position: relative;
      text-align: center;
      padding: 17px 20px;
      margin-right: 70px;
      line-height: 1.2;
      &.is-active {
        text-shadow: 1px 0 0 currentColor;
        &::before {
          content: '';
          display: block;
          position: absolute;
          width: 100%;
          height: 5px;
          background-color: $yellow;
          bottom: 0;
          left: 0; }
        &:nth-child(2) {
          &::before {
            background-color: #8ed3d3; } } } } }

  .right {
    float: right; }

  select {
    appearance: none;
    color: $white;
    min-width: 135px;
    border-radius: 2px;
    border: none;
    padding: 5px 10px;
    font-size: 12px;
    font-weight: 400;
    background: {
      color: $gray;
      image: url('data:image/png;base64,');
      size: 12px;
      position: calc(100% - 10px) 50%;
      repeat: no-repeat; } }

  .o-pane__contents {
    padding-top: 25px;
    font-family: $arial;
    font-size: 13px;

    h1 {
      font-family: $arial;
      font-size: 12px;
      font-weight: 400;
      margin-bottom: 20px; } } }

select::-ms-expand {
  display: none; }

.o-pane--yellow,
.o-pane--blue,
.o-pane--green {
  @extend %o-pane; }


.o-pane--yellow {
  header {
    .o-pane__dot {
      background-color: $yellow; } } }

.o-pane--blue {
  header {
    .o-pane__dot {
      background-color: $blue; } } }

.o-pane--green {
  header {
    .o-pane__dot {
      background-color: $green; } } }
