// =======================================
// =            Global Config            =
// =======================================

// Typography

// Use this to insert a Google Font the Import Function Comes from Manila Mixins
// If you won't use Google Fonts you can use Null or remove the line.
$font-url--google       : "http://fonts.googleapis.com/css?family=Raleway:400,500,700";
$font-family--primary   : "Raleway", "Helvetica Neue", Helvetica, Arial, sans-serif;

$raleway                : $font-family--primary;
$arial                  : Arial, sans-serif;
// Text
$base__font-size        : 16px;
$base__line             : 1.65;

// Modular Scale - https://github.com/modularscale/modularscale-sass/tree/3.x
$ms-base                :  1rem;
$ms-ratio               : $major-third;

// Stuff for the BassCss-like classes
$total-stops            : 5;

// Typographic Weights
$fw--thin               : 100;
$fw__extra-light        : 200;
$fw--light              : 300;
$fw--regular            : 400;
$fw--medium             : 500;
$fw--semi-bold          : 600;
$fw--bold               : 700;
$fw--extra-bold         : 800;
$fw--ultra-bold         : 900;

// Colors
$white                  : #ffffff;
$black                  : #1b2325;
$gray                   : #3E444A;

$red                    : #F06a5c;
$blue                   : #5AA0D8;
$green                  : #55C1A9;
$yellow                 : #f5d74c;

$main-color             : $red;

// Transitions
$trns-duration          : .3s;

// Grid
$column-gutter          : 20px;
$columns--total         : 12;

// Breakpoints
$breakpoints: (
  'desktop'     : em(960px)
);
