@include object('header') {
  display: flex;
  align-items: center;
  margin-bottom: 40px;

  @include element('title') {
    margin-right: auto;
    font-size: 18px; }

  @include element('profile') {
    @include size(60px);
    background-color: #cdcdcd;
    position: relative;
    border-radius: 50%;

    span {
      @include align;
      font-size: 25px;
      font-weight: 500;
      text-transform: uppercase; } } }
