@include object('menu') {
  text-transform: uppercase;
  font-size: 15px;

  @include element('logo') {
    display: block;
    padding: 0 40px;
    margin-bottom: 40px; }

  @include element('links') {
    font-family: $raleway;

    li {
      padding: 0 40px; }

    a {
      display: block;
      padding: 40px 0;
      border-bottom: 1px solid #323c3d;
      position: relative;
      @include state('active') {
        font-weight: 700;
        &::before {
          content: '';
          display: block;
          width: 10px;
          height: 100%;
          background: $red;
          position: absolute;
          top: 0;
          left: -50px; } } } } }
