/// This just magically works, it looks for the `$font-url--google` and if it exists will import it. Simple!
/// @example scss Input
///   $font-url--google: "https://fonts.googleapis.com/css?family=Lato:300";
/// @example css Output
///   @import url("https://fonts.googleapis.com/css?family=Lato:300");

@if variable-exists(font-url--google) and type-of($font-url--google) == string {
  @import url($font-url--google); }
